.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--primary-bg);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.loading-content {
  text-align: center;
}

.loading-content h1 {
  color: var(--text-primary);
  font-size: 2.5rem;
  margin-bottom: 2rem;
}

.progress-bar {
  width: 300px;
  height: 4px;
  background-color: var(--secondary-bg);
  border-radius: 2px;
  overflow: hidden;
  margin: 1rem auto;
}

.progress-fill {
  height: 100%;
  background-color: var(--text-primary);
  transition: width 0.2s ease-out;
}

.progress-text {
  color: var(--text-secondary);
  font-size: 0.9rem;
  margin-top: 0.5rem;
} 