.footer {
  background: var(--secondary-bg);
  border-top: 1px solid var(--border-color);
  padding: 16px 24px;
  margin-top: auto;
  width: 100%;
  flex-shrink: 0;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-section {
  display: flex;
  align-items: center;
  gap: 24px;
}

.copyright {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.footer-nav {
  display: flex;
  gap: 24px;
}

.footer-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.2s;
}

.footer-link:hover {
  color: var(--text-primary);
}

.social-links {
  display: flex;
  gap: 16px;
}

.social-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 0.9rem;
  transition: color 0.2s;
}

.social-link:hover {
  color: var(--text-primary);
}

@media (max-width: 768px) {
  .footer-content {
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }

  .footer-section {
    justify-content: center;
  }

  .footer-nav {
    gap: 16px;
  }
} 