.subscription-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  padding: 24px;
}

.subscription-card {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid var(--border-color);
}

/* Current Plan styles */
.current-plan {
  display: flex;
  gap: 16px;
  margin-bottom: 20px;
}

.plan-badge {
  padding: 8px 16px;
  border-radius: 4px;
  font-weight: 500;
  font-size: 0.9rem;
}

.plan-badge.pro {
  background: var(--accent-color);
  color: white;
}

.plan-details {
  flex: 1;
}

.plan-info {
  margin-bottom: 8px;
}

.plan-name {
  display: block;
  color: var(--text-primary);
  font-weight: 500;
  margin-bottom: 4px;
}

.plan-price {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.plan-status {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.status-label {
  color: #10B981;
  font-size: 0.9rem;
}

.renewal-date {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.cancel-button {
  width: 100%;
  padding: 8px;
  border: 1px solid #EF4444;
  border-radius: 4px;
  color: #EF4444;
  font-size: 0.9rem;
  transition: all 0.2s;
}

.cancel-button:hover {
  background: rgba(239, 68, 68, 0.1);
}

/* Pricing Plans styles */
.plans-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 16px;
}

.plan-card {
  position: relative;
  padding: 20px;
  background: var(--primary-bg);
  border-radius: 8px;
  border: 1px solid var(--border-color);
}

.plan-card.recommended {
  border-color: var(--accent-color);
}

.recommended-badge {
  position: absolute;
  top: -12px;
  left: 50%;
  transform: translateX(-50%);
  padding: 4px 12px;
  background: var(--accent-color);
  color: white;
  border-radius: 12px;
  font-size: 0.8rem;
}

.plan-header {
  text-align: center;
  margin-bottom: 16px;
}

.plan-pricing {
  margin-top: 8px;
}

.price {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--text-primary);
}

.period {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.plan-features {
  list-style: none;
  padding: 0;
  margin: 0 0 20px;
}

.plan-features li {
  color: var(--text-secondary);
  font-size: 0.9rem;
  padding: 8px 0;
  border-bottom: 1px solid var(--border-color);
}

.plan-features li:last-child {
  border-bottom: none;
}

.select-plan-button {
  width: 100%;
  padding: 8px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  transition: all 0.2s;
}

.select-plan-button.primary {
  background: var(--accent-color);
  border-color: var(--accent-color);
  color: white;
}

.select-plan-button:hover {
  background: rgba(255, 255, 255, 0.05);
}

.select-plan-button.primary:hover {
  background: var(--accent-color-dark);
}

/* Billing Info styles */
.billing-info {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.payment-method {
  display: flex;
  align-items: center;
  gap: 16px;
  padding: 16px;
  background: var(--primary-bg);
  border-radius: 8px;
}

.card-icon {
  font-size: 24px;
}

.card-details {
  flex: 1;
}

.card-number {
  display: block;
  color: var(--text-primary);
  margin-bottom: 4px;
}

.card-expiry {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.billing-address h4 {
  color: var(--text-primary);
  margin: 0 0 8px;
  font-size: 1rem;
}

.billing-address p {
  color: var(--text-secondary);
  font-size: 0.9rem;
  line-height: 1.5;
  margin: 0;
}

.edit-button {
  padding: 4px 12px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

/* Payment History styles */
.payments-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.payment-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px;
  background: var(--primary-bg);
  border-radius: 4px;
}

.payment-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.payment-date {
  color: var(--text-primary);
  font-size: 0.9rem;
}

.payment-amount {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.payment-details {
  display: flex;
  align-items: center;
  gap: 12px;
}

.payment-status {
  color: #10B981;
  font-size: 0.9rem;
}

.invoice-button {
  display: flex;
  align-items: center;
  gap: 4px;
  padding: 4px 8px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.invoice-icon {
  font-size: 16px;
} 