/* DateRangePicker styles */
.date-picker {
  display: flex;
  gap: 8px;
  margin-bottom: 24px;
}

.date-preset {
  padding: 8px 16px;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  background: transparent;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s;
}

.date-preset:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
}

.date-preset.active {
  background: var(--accent-color);
  border-color: var(--accent-color);
  color: white;
}

.date-preset.custom {
  margin-left: auto;
}

/* StatsFilters styles */
.stats-filters {
  display: flex;
  gap: 24px;
  margin-bottom: 24px;
}

.filter-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.filter-group label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.filter-select {
  padding: 8px 12px;
  background: var(--primary-bg);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 0.9rem;
  min-width: 160px;
}

/* ExportData styles */
.export-controls {
  margin-bottom: 24px;
}

.export-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  cursor: pointer;
  transition: all 0.2s;
}

.export-button:hover {
  background: rgba(255, 255, 255, 0.05);
}

/* SitesList styles */
.sites-table {
  width: 100%;
  border: 1px solid var(--border-color);
  border-radius: 8px;
  overflow: hidden;
}

.table-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
  background: var(--primary-bg);
  border-bottom: 1px solid var(--border-color);
}

.header-cell {
  padding: 12px 16px;
  color: var(--text-secondary);
  font-size: 0.9rem;
  font-weight: 500;
}

.table-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 1fr 1fr;
  border-bottom: 1px solid var(--border-color);
  transition: background-color 0.2s;
}

.table-row:last-child {
  border-bottom: none;
}

.table-row:hover {
  background: rgba(255, 255, 255, 0.02);
}

.cell {
  padding: 12px 16px;
  color: var(--text-primary);
  font-size: 0.9rem;
  display: flex;
  align-items: center;
}

.site-cell {
  display: flex;
  align-items: center;
  gap: 12px;
}

.site-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.stats-controls {
  background: var(--secondary-bg);
  border: 1px solid var(--border-color);
  border-radius: 8px;
  padding: 24px;
  margin-top: 24px;
  margin-bottom: 24px;
} 