.settings-sections {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  padding: 24px;
}

.settings-card {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid var(--border-color);
}

.card-title {
  color: var(--text-primary);
  font-size: 1.1rem;
  margin: 0 0 16px;
  font-weight: 500;
}

/* Settings List styles */
.settings-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.setting-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 24px;
}

.setting-info {
  flex: 1;
}

.setting-name {
  display: block;
  color: var(--text-primary);
  font-size: 0.95rem;
  margin-bottom: 4px;
}

.setting-description {
  color: var(--text-secondary);
  font-size: 0.85rem;
}

/* Toggle Switch styles */
.toggle {
  position: relative;
  display: inline-block;
  width: 44px;
  height: 24px;
}

.toggle input {
  opacity: 0;
  width: 0;
  height: 0;
}

.toggle-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: var(--primary-bg);
  transition: .4s;
  border-radius: 24px;
}

.toggle-slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: var(--text-secondary);
  transition: .4s;
  border-radius: 50%;
}

input:checked + .toggle-slider {
  background-color: var(--accent-color);
}

input:checked + .toggle-slider:before {
  transform: translateX(20px);
  background-color: white;
}

/* Select styles */
.setting-select {
  padding: 8px 12px;
  background: var(--primary-bg);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 0.9rem;
  min-width: 140px;
}

/* Site Exclusions styles */
.exclusions-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 16px;
}

.exclusion-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: var(--primary-bg);
  border-radius: 4px;
}

.site-name {
  color: var(--text-primary);
  font-size: 0.9rem;
}

.remove-button {
  padding: 4px 8px;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: #EF4444;
  font-size: 0.8rem;
  cursor: pointer;
}

.add-exclusion {
  display: flex;
  gap: 8px;
}

.exclusion-input {
  flex: 1;
  padding: 8px 12px;
  background: var(--primary-bg);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 0.9rem;
}

.add-button {
  padding: 8px 16px;
  background: var(--accent-color);
  border: none;
  border-radius: 4px;
  color: white;
  font-size: 0.9rem;
  cursor: pointer;
}

/* Sync Actions styles */
.sync-actions {
  display: flex;
  gap: 12px;
  margin-top: 20px;
}

.sync-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px 16px;
  background: var(--accent-color);
  border: none;
  border-radius: 4px;
  color: white;
  font-size: 0.9rem;
  cursor: pointer;
}

.danger-button {
  padding: 8px 16px;
  background: transparent;
  border: 1px solid #EF4444;
  border-radius: 4px;
  color: #EF4444;
  font-size: 0.9rem;
  cursor: pointer;
}

.danger-button:hover {
  background: rgba(239, 68, 68, 0.1);
} 