.auth-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
}

.auth-container {
  width: 100%;
  max-width: 400px;
  background: var(--secondary-bg);
  padding: 32px;
  border-radius: 8px;
  border: 1px solid var(--border-color);
  position: relative;
}

.header-container {
  display: flex;
  align-items: center;
  gap: 16px;
  margin-bottom: 8px;
}

.back-button {
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  width: 36px;
  padding: 0;
  flex-shrink: 0;
}

.back-button:hover {
  background: rgba(255, 255, 255, 0.05);
  border-color: var(--text-primary);
  color: var(--text-primary);
}

.auth-container h1 {
  font-size: 1.8rem;
  margin: 0;
  color: var(--text-primary);
}

.auth-description {
  color: var(--text-secondary);
  margin: 8px 0 24px;
}

.auth-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.form-group label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.form-group input {
  padding: 12px;
  background: var(--primary-bg);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-primary);
  font-size: 1rem;
}

.form-group input:focus {
  outline: none;
  border-color: var(--accent-color);
}

.auth-submit {
  background: var(--accent-color);
  color: white;
  border: none;
  padding: 12px;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  margin-top: 8px;
  transition: background-color 0.2s;
}

.auth-submit:hover {
  background: var(--accent-color-dark);
}

.auth-links {
  margin-top: 24px;
  text-align: center;
}

.auth-link {
  color: var(--accent-color);
  text-decoration: none;
  font-size: 0.9rem;
}

.auth-link:hover {
  text-decoration: underline;
}

.auth-separator {
  margin-top: 16px;
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.auth-error {
  background: rgba(220, 38, 38, 0.1);
  color: rgb(220, 38, 38);
  padding: 12px;
  border-radius: 4px;
  font-size: 0.9rem;
  margin-bottom: 20px;
  text-align: center;
}

.recaptcha-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100px;
  margin: 16px 0;
}

.h-captcha {
  display: flex;
  justify-content: center;
}

.grecaptcha-badge {
  visibility: hidden;
}

.auth-success {
  background: rgba(16, 185, 129, 0.1);
  border: 1px solid #10B981;
  color: #10B981;
  padding: 12px;
  border-radius: 8px;
  margin-bottom: 16px;
  text-align: center;
} 