.dashboard-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  padding: 24px;
}

.dashboard-card {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid var(--border-color);
}

.card-title {
  color: var(--text-primary);
  font-size: 1.1rem;
  margin: 0 0 16px;
  font-weight: 500;
}

/* DailyStats styles */
.stats-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}

.stat-item {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.stat-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.stat-value {
  color: var(--text-primary);
  font-size: 1.2rem;
  font-weight: 500;
}

/* TopSites styles */
.sites-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.site-item {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 8px;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.site-item:hover {
  background: rgba(255, 255, 255, 0.05);
}

.site-icon {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.site-name {
  flex: 1;
  color: var(--text-primary);
  font-size: 0.9rem;
}

.site-time {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

/* ActivityGraph styles */
.graph-placeholder {
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--primary-bg);
  border-radius: 4px;
  border: 1px dashed var(--border-color);
}

.graph-message {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

/* TrafficOverview styles */
.traffic-stats {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.traffic-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.traffic-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.traffic-value {
  color: var(--text-primary);
  font-size: 1rem;
  font-weight: 500;
}

/* Добавим к существующим стилям */
.timeline-chart {
  height: 300px;
  margin-top: 16px;
  background: var(--primary-bg);
  border-radius: 4px;
  padding: 16px;
} 