.layout {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: var(--primary-bg);
  overflow: hidden;
}

.content-wrapper {
  flex: 1;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  padding: 0 24px;
}

.main-content {
  flex: 1;
  width: 100%;
  min-height: 0;
  overflow-y: auto;
}

@media (max-width: 768px) {
  .content-wrapper {
    padding: 0 16px;
  }
} 