.chat-button {
  position: fixed;
  bottom: 24px;
  right: 24px;
  padding: 12px 20px;
  background: var(--accent-color);
  color: white;
  border: none;
  border-radius: 24px;
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
  transition: all 0.2s;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  z-index: 999;
}

.chat-button-content {
  display: flex;
  align-items: center;
  gap: 8px;
}

.chat-notification {
  background: #EF4444;
  color: white;
  min-width: 20px;
  height: 20px;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.8rem;
  font-weight: 600;
  padding: 0 6px;
}

.chat-button:hover {
  background: var(--accent-color-dark);
  transform: translateY(-2px);
}

.chat-button-text {
  font-weight: 500;
}

@media (max-width: 768px) {
  .chat-button {
    bottom: 16px;
    right: 16px;
  }
} 