.welcome-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  text-align: center;
}

.welcome-container {
  width: 100%;
  max-width: 500px;
  padding: 48px 32px;
}

.welcome-container h1 {
  font-size: 2.5rem;
  margin: 0;
  color: var(--text-primary);
}

.welcome-description {
  color: var(--text-secondary);
  font-size: 1.1rem;
  margin: 16px 0 32px;
}

.welcome-buttons {
  display: flex;
  gap: 16px;
  justify-content: center;
}

.welcome-button {
  padding: 12px 24px;
  border-radius: 4px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.welcome-button.primary {
  background: var(--accent-color);
  border: none;
  color: white;
}

.welcome-button.primary:hover {
  background: var(--accent-color-dark);
}

.welcome-button.secondary {
  background: transparent;
  border: 1px solid var(--border-color);
  color: var(--text-primary);
}

.welcome-button.secondary:hover {
  background: rgba(255, 255, 255, 0.1);
} 