.productivity-sections {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  padding: 24px;
}

.productivity-card {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid var(--border-color);
}

.card-title {
  color: var(--text-primary);
  font-size: 1.1rem;
  margin: 0 0 16px;
  font-weight: 500;
}

/* ProductivityScore styles */
.score-container {
  display: flex;
  gap: 24px;
  align-items: center;
}

.score-circle {
  position: relative;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 8px solid var(--accent-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.score-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
}

.score-value {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
}

.score-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.score-details {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.score-stat {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* CategoryManager styles */
.categories-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 16px;
}

.category-item {
  background: var(--primary-bg);
  border-radius: 4px;
  padding: 12px;
}

.category-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 8px;
}

.category-color {
  width: 16px;
  height: 16px;
  border-radius: 4px;
}

.category-name {
  flex: 1;
  color: var(--text-primary);
  font-weight: 500;
}

.category-edit {
  padding: 4px 8px;
  background: transparent;
  border: 1px solid var(--border-color);
  border-radius: 4px;
  color: var(--text-secondary);
  cursor: pointer;
}

.category-sites {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}

.site-tag {
  padding: 4px 8px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  font-size: 0.8rem;
  color: var(--text-secondary);
}

/* TimeLimits styles */
.limits-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 16px;
}

.limit-item {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.limit-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.limit-category {
  color: var(--text-primary);
  font-size: 0.9rem;
}

.limit-time {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.limit-progress {
  height: 6px;
  background: var(--primary-bg);
  border-radius: 3px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background: var(--accent-color);
  border-radius: 3px;
  transition: width 0.3s ease;
}

/* Recommendations styles */
.recommendations-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.recommendation-item {
  display: flex;
  gap: 12px;
  padding: 12px;
  background: var(--primary-bg);
  border-radius: 4px;
}

.recommendation-icon {
  font-size: 20px;
}

.recommendation-content {
  flex: 1;
}

.recommendation-title {
  color: var(--text-primary);
  font-size: 0.95rem;
  margin: 0 0 4px;
}

.recommendation-description {
  color: var(--text-secondary);
  font-size: 0.85rem;
  margin: 0;
}

.recommendation-item.warning {
  border-left: 3px solid #F59E0B;
}

.recommendation-item.success {
  border-left: 3px solid #10B981;
}

.recommendation-item.info {
  border-left: 3px solid #3B82F6;
}

/* Common button styles */
.add-category-button,
.add-limit-button {
  width: 100%;
  padding: 8px;
  background: transparent;
  border: 1px dashed var(--border-color);
  border-radius: 4px;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.2s;
}

.add-category-button:hover,
.add-limit-button:hover {
  background: rgba(255, 255, 255, 0.05);
  color: var(--text-primary);
} 