.toolbar {
  background-color: var(--secondary-bg);
  height: 60px;
  display: flex;
  align-items: center;
  padding: 0 24px;
  border-bottom: 1px solid var(--border-color);
  justify-content: space-between;
}

.toolbar-left {
  display: flex;
  align-items: center;
}

.app-logo {
  color: var(--text-primary);
  font-size: 1.5rem;
  font-weight: 600;
  text-decoration: none;
  margin-right: 48px;
  transition: color 0.2s;
}

.app-logo:hover {
  color: var(--accent-color);
}

.toolbar-nav {
  display: flex;
  gap: 32px;
}

.nav-link {
  color: var(--text-secondary);
  text-decoration: none;
  font-size: 0.9rem;
  font-weight: 500;
  padding: 6px 0;
  transition: all 0.2s;
  position: relative;
}

.nav-link:hover {
  color: var(--text-primary);
}

.nav-link.active {
  color: var(--text-primary);
}

.nav-link.active::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  height: 2px;
  background-color: var(--accent-color);
  border-radius: 1px;
}

.toolbar-right {
  display: flex;
  align-items: center;
}

.auth-buttons {
  display: flex;
  gap: 12px;
}

.auth-button {
  padding: 8px 16px;
  border-radius: 4px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  background: transparent;
  border: 1px solid var(--border-color);
  color: var(--text-primary);
}

.auth-button:hover {
  background: rgba(255, 255, 255, 0.1);
}

.auth-button.primary {
  background: var(--accent-color);
  border-color: var(--accent-color);
  color: white;
}

.auth-button.primary:hover {
  background: var(--accent-color-dark);
}

.user-menu {
  display: flex;
  align-items: center;
  gap: 16px;
}

.user-email {
  display: flex;
  align-items: center;
  gap: 8px;
  color: var(--text-secondary);
  font-size: 0.9rem;
  text-decoration: none;
  padding: 4px;
  border-radius: 4px;
  transition: all 0.2s;
}

.user-email:hover {
  color: var(--text-primary);
  background: rgba(255, 255, 255, 0.05);
}

.user-avatar {
  width: 28px;
  height: 28px;
  background: var(--accent-color);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 500;
  font-size: 0.9rem;
}

.user-profile {
  display: flex;
  align-items: center;
  gap: 12px;
  text-decoration: none;
  padding: 4px;
  border-radius: 4px;
  transition: background-color 0.2s;
}

.user-profile:hover {
  background: rgba(255, 255, 255, 0.05);
}

.user-avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}

.user-avatar-placeholder {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: linear-gradient(45deg, var(--accent-color), var(--accent-color-dark));
  color: white;
  font-size: 1rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
}

.user-email {
  color: var(--text-secondary);
  font-size: 0.9rem;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
} 