/* Общие стили для всех страниц */
.page-container {
  padding: 24px;
  max-width: 1200px; /* Возвращаем стандартную ширину */
  margin: 0 auto;
  width: 100%;
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 0;
}

/* Специальные стили для страницы настроек */
.page-container.settings-page {
  max-width: 1200px;
}

.page-header {
  margin-bottom: 16px; /* Унифицированный отступ после заголовка */
  flex-shrink: 0; /* Предотвращаем сжатие заголовка */
}

.page-header h1 {
  font-size: 2rem;
  font-weight: 600;
  color: var(--text-primary);
  margin: 0 0 8px;
}

.page-description {
  font-size: 1rem;
  color: var(--text-secondary);
  margin: 0;
}

.page-content {
  background-color: var(--secondary-bg);
  border-radius: 8px;
  padding: 24px;
  margin-left: 24px;
  margin-right: 24px;
  border: 1px solid var(--border-color);
}

.account-info {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.info-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.info-group label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.info-value {
  color: var(--text-primary);
  font-size: 1rem;
  padding: 12px;
  background: var(--primary-bg);
  border-radius: 4px;
  border: 1px solid var(--border-color);
}

/* Общие стили для всех карточек/секций */
.productivity-card,
.subscription-card,
.settings-card,
.report-card,
.statistics-card {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid var(--border-color);
}

/* Заголовок секции */
.card-title {
  color: var(--text-primary);
  font-size: 1.1rem;
  margin: 0 0 16px;
  font-weight: 500;
}

/* Grid layouts с фиксированной высотой и скроллом */
.dashboard-grid,
.reports-grid,
.productivity-sections,
.settings-sections,
.subscription-grid,
.statistics-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  height: 70vh;
  gap: 24px;
  overflow: auto;
  min-height: 0;
}

/* Убираем overflow из всех внутренних элементов */
.sites-table,
.trends-list,
.categories-list,
.recommendations-list,
.payments-list,
.plans-grid {
  background: var(--primary-bg);
  border-radius: 4px;
  border: 1px solid var(--border-color);
}

/* Стили для списков с элементами */
.trend-item,
.category-item,
.recommendation-item,
.payment-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid var(--border-color);
}

.trend-item:last-child,
.category-item:last-child,
.recommendation-item:last-child,
.payment-item:last-child {
  border-bottom: none;
}

.trend-category {
  color: var(--text-primary);
}

.trend-change {
  font-weight: 500;
}

.trend-change.increasing {
  color: #10B981;
}

.trend-change.decreasing {
  color: #EF4444;
}

.trend-change.stable {
  color: #F59E0B;
}

/* Специальные стили для subscription-grid */
.subscription-grid {
  grid-template-areas:
    "current plans"
    "billing history";
}

.subscription-grid > *:nth-child(1) { grid-area: current; }
.subscription-grid > *:nth-child(2) { grid-area: plans; }
.subscription-grid > *:nth-child(3) { grid-area: billing; }
.subscription-grid > *:nth-child(4) { grid-area: history; }

/* Стили для плейсхолдеров графиков и диаграмм */
.report-chart-placeholder,
.productivity-chart-placeholder,
.trends-chart-placeholder {
  height: 200px; /* Уменьшаем высоту графиков */
  background: var(--primary-bg);
  border-radius: 4px;
  border: 1px dashed var(--border-color);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--text-secondary);
  margin: 16px 0;
}

/* Responsive styles */
@media (max-width: 1024px) {
  .dashboard-grid,
  .reports-grid,
  .productivity-sections,
  .settings-sections,
  .subscription-grid {
    grid-template-columns: 1fr;
    grid-template-areas:
      "current"
      "plans"
      "billing"
      "history";
  }
}

@media (max-width: 768px) {
  .page-container {
    padding: 16px;
  }

  .page-header h1 {
    font-size: 1.5rem;
  }
}

/* Стили для контейнера фильтров в Statistics */
.stats-controls {
  margin-bottom: 16px; /* Такой же отступ как у page-header */
  display: flex;
  margin-left: 24px;
  margin-right: 24px;
  flex-direction: column;
  gap: 16px;
}

.stats-filters {
  display: flex;
  gap: 16px;
  align-items: center;
}

.filter-group {
  display: flex;
  gap: 8px;
  align-items: center;
}

.date-picker {
  display: flex;
  gap: 8px;
}

/* Обновляем стили для таблицы */
.sites-table {
  margin-top: 16px;
  background: var(--secondary-bg);
  border-radius: 8px;
  border: 1px solid var(--border-color);
  height: 300px; /* Фиксированная высота для таблицы */
}

/* Добавьте к существующим стилям */
.legal-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 24px;
  background: var(--secondary-bg);
  border-radius: 8px;
  border: 1px solid var(--border-color);
}

.legal-section {
  margin-bottom: 32px;
}

.legal-section h2 {
  color: var(--text-primary);
  font-size: 1.5rem;
  margin-bottom: 16px;
}

.legal-section p {
  color: var(--text-secondary);
  line-height: 1.6;
  margin-bottom: 16px;
}

.legal-section ul {
  color: var(--text-secondary);
  padding-left: 24px;
  margin-bottom: 16px;
}

.legal-section li {
  margin-bottom: 8px;
  line-height: 1.6;
}

/* Добавим стили для боковой рекламы */
.page-layout {
  display: flex;
  min-height: 100vh;
}

.side-ad {
  width: 160px;
  min-height: 600px;
  position: sticky;
  top: 24px;
  padding: 10px;
  background: var(--secondary-bg);
  border-radius: 8px;
  margin: 24px 12px;
  flex-shrink: 0;
}

.side-ad.left {
  margin-right: 24px;
}

.side-ad.right {
  margin-left: 24px;
}

/* Адаптивность */
@media (max-width: 1400px) {
  .side-ad {
    display: none;
  }
}

/* Обновим стили для основного контента */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

/* Обновим стили для auth страниц */
.auth-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  overflow: hidden;
}

/* Обновим стили для welcome страницы */
.welcome-page {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px;
  overflow: hidden;
}

/* Добавим к существующим стилям */
.avatar-section {
  display: flex;
  justify-content: center;
  margin: 32px 0;
}

.avatar-container {
  position: relative;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.avatar-image:hover {
  transform: scale(1.05);
}

.avatar-placeholder {
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, var(--accent-color), var(--accent-color-dark));
  color: white;
  font-size: 3.5rem;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
}

.avatar-upload-label {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  padding: 8px 0;
  text-align: center;
  opacity: 0;
  transition: opacity 0.3s ease;
  cursor: pointer;
}

.avatar-container:hover .avatar-upload-label {
  opacity: 1;
}

.avatar-upload-button {
  color: white;
  font-size: 0.9rem;
  font-weight: 500;
}

.avatar-upload-button:hover {
  text-decoration: underline;
}

/* Стили для страницы настроек */
.settings-wrapper {
  background: var(--secondary-bg);
  border-radius: 8px;
  border: 1px solid var(--border-color);
  margin-bottom: 24px;
  width: 100%;
  overflow: hidden;
}

.settings-content {
  padding: 32px;
  display: flex;
  flex-direction: column;
  gap: 32px;
}

.settings-section {
  display: flex;
  flex-direction: column;
  gap: 24px;
  width: 100%;
}

.section-title {
  font-size: 1.4rem;
  color: var(--text-primary);
  margin: 0;
  padding-bottom: 16px;
  border-bottom: 1px solid var(--border-color);
}

/* Группы настроек */
.settings-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
  width: 100%;
}

.settings-group label {
  color: var(--text-primary);
  font-size: 0.95rem;
  font-weight: 500;
}

/* Поля ввода */
.settings-input,
.settings-select,
.settings-textarea,
.settings-value,
.checkbox-label {
  width: 100%;
  padding: 12px;
  background: var(--primary-bg);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  color: var(--text-primary);
  font-size: 0.95rem;
  transition: border-color 0.2s;
  box-sizing: border-box;
}

.settings-textarea {
  min-height: 120px;
  height: 120px;
  resize: none;
  font-family: inherit;
}

/* Чекбоксы */
.checkbox-label {
  display: flex;
  align-items: center;
  gap: 12px;
  cursor: pointer;
}

.settings-checkbox {
  width: 18px;
  height: 18px;
  accent-color: var(--accent-color);
}

/* Счетчик символов */
.input-counter {
  position: relative;
  width: 100%;
}

.char-counter {
  position: absolute;
  bottom: 12px;
  right: 12px;
  font-size: 0.8rem;
  color: var(--text-secondary);
  background: transparent;
  padding: 2px 6px;
  border-radius: 4px;
  opacity: 0.8;
}

/* Кнопка сохранения */
.settings-actions {
  position: sticky;
  bottom: 0;
  background: var(--secondary-bg);
  padding: 16px 32px;
  border-top: 1px solid var(--border-color);
  display: flex;
  justify-content: flex-end;
  z-index: 10;
}

.settings-save {
  min-width: 200px;
  padding: 12px 32px;
  background: var(--accent-color);
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 0.95rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

/* Удалим все дублирующиеся стили и max-width ограничения */ 