.reports-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 24px;
  padding: 24px;
}

.report-card {
  background: var(--secondary-bg);
  border-radius: 8px;
  padding: 20px;
  border: 1px solid var(--border-color);
}

.card-title {
  color: var(--text-primary);
  font-size: 1.1rem;
  margin: 0 0 16px;
  font-weight: 500;
}

/* Report Stats styles */
.report-stats {
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-bottom: 20px;
}

.report-stat {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.stat-label {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

.stat-value {
  color: var(--text-primary);
  font-size: 1.4rem;
  font-weight: 500;
}

.stat-change {
  font-size: 0.9rem;
}

.stat-change.positive {
  color: #10B981;
}

.stat-change.negative {
  color: #EF4444;
}

.stat-detail {
  color: var(--text-secondary);
  font-size: 0.9rem;
}

/* Trends styles */
.trends-list {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 20px;
}

.trend-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 12px;
  background: var(--primary-bg);
  border-radius: 4px;
}

.trend-category {
  color: var(--text-primary);
  font-size: 0.9rem;
}

.trend-change {
  font-size: 0.9rem;
  font-weight: 500;
}

.trend-change.increasing {
  color: #10B981;
}

.trend-change.decreasing {
  color: #EF4444;
}

.trend-change.stable {
  color: #F59E0B;
}

/* Export Options styles */
.export-options {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 12px;
}

.export-option {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 16px;
  background: var(--primary-bg);
  border: 1px solid var(--border-color);
  border-radius: 4px;
  cursor: pointer;
  transition: all 0.2s;
}

.export-option:hover {
  background: rgba(255, 255, 255, 0.05);
}

.export-icon {
  font-size: 24px;
}

.export-name {
  color: var(--text-primary);
  font-size: 0.9rem;
}

/* Chart placeholder styles */
.report-chart-placeholder {
  display: none;
}

.report-chart {
  height: 200px;
  margin-top: 16px;
  background: var(--primary-bg);
  border-radius: 4px;
  padding: 16px;
  width: 94%;
  min-height: 200px;
  position: relative;
}

.recharts-legend-wrapper {
  padding: 8px !important;
}

.recharts-legend-item-text {
  color: var(--text-secondary) !important;
}

/* Стили для ResponsiveContainer */
.recharts-responsive-container {
  width: 100% !important;
  height: 100% !important;
}

/* Стили для сетки графика */
.recharts-cartesian-grid-horizontal line,
.recharts-cartesian-grid-vertical line {
  stroke: var(--border-color);
}

/* Стили для текста на осях */
.recharts-text {
  fill: var(--text-secondary);
}

/* Стили для тултипа */
.recharts-tooltip-wrapper {
  background: var(--secondary-bg) !important;
  border: 1px solid var(--border-color) !important;
  border-radius: 4px;
}

/* Добавляем стили для контейнера графика */
.recharts-wrapper {
  width: 100% !important;
  height: 100% !important;
} 